import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col } from 'antd';
import FormInfoCommonYears from './FormInfoCommonYears';
import MenuTabTop from './MenuTabTop';
import TableListSummarizeYears from './TableListSummarizeYears';
import './style.scss';

class ListSummarizePointsYearsPage extends Component {
  state = {};
  render() {
    return (
      <Card title="Kết quả học tập">
        <Row className="rowMargin">
          <Col span={24}>
            <MenuTabTop />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <FormInfoCommonYears />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableListSummarizeYears />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListSummarizePointsYearsPage />
  </Page>
);



